@import url(https://fonts.googleapis.com/css?family=B612+Mono|Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  /* margin: 0 auto; */
  text-align: center;
  background-color: #ffffff;
  font-family: 'Lato', sans-serif;
}
.container{
  display: flex;
  padding: 10px 15px;
}
.col-1{
  background-image: url(/v9-geckos-team-04/static/media/speech_to_text.04f03d28.svg);
  flex:1 1;
  background-repeat: no-repeat;
  background-size: contain;
}
.col-2{
  flex:1 1;
}
.subTitle{
  font-weight: 300;
  color:#6c61f6;
}

.textarea{
  overflow: auto;
  width: 80%;
  height: 300px;
  margin: 20px 20px;
  border:none;
  border-radius:10px;
  box-shadow: 0 0 3px 5px rgba(0,0,0,0.12);
  padding:15px;
  font-size:14px;
}

.pulse {
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  -webkit-animation: pulse 2s infinite ease-in-out;
          animation: pulse 2s infinite ease-in-out;
}

@-webkit-keyframes pulse {
  0%    {opacity: 1;}
  50%   {opacity: .3;}
  100%  {opacity: 1;}
}

@keyframes pulse {
  0%    {opacity: 1;}
  50%   {opacity: .3;}
  100%  {opacity: 1;}
}

@media (min-width: 320px) and (max-width: 480px) {
  
  .col-1{
    display: none;
  }
  .container {
    flex-direction: column;
  }
  .title{
    font-size: 45px !important;
  }
  .textarea{
    height: 246px;
  }
}
.title{
    color:#453adf;
    font-size: 65px;
    font-family: 'B612 Mono', monospace;
    margin:8px 0;
  }
.footer{
    padding:8px;
    color: #ffffff;
    background-color: #4c4c56;
    font-weight: 300;
  }
  .footer p{
      margin:5px;
  }
.btn{
    padding:10px;
    background-color: #39364d;
    color:#ffffff;
    font-size:14px;
    border-radius:5px;
    font-weight:bold;
    margin:0 5px 20px 5px;
  }
.btn:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1); 
}
.btn :active{
    background-color: #ffffff;
    color: #2c274b
}
  .start, .stop {
    padding:10px;
    background-color: #6c61f6;
    color:#ffffff;
    font-weight: bold;
    font-size: 14px;
    border-radius:5px;
  }

  .start:hover, .stop :hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1); 
  }
